@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.page {
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
}

.form {
  background: var(--bg);
  border: 1px solid #ebebeb;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.08);
  border-radius: 8px;
  padding: 20px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include notebook {
    margin: 10px 0 10px;
    text-align: center;
  }

  &__group {
    margin-top: 10px;
    width: 100%;
    max-width: 410px;
  }
}

.instructions {
    margin-top: 20px;
    background: var(--secondary-bg);
    border-radius: 8px;
    padding: 10px;
    margin: 15px auto 0;
    width: fit-content;
    max-width: 100%;

    &__heading {
        font-weight: 500;
        margin-bottom: 5px;
    }

    &__list {
        list-style: decimal inside;
        text-align: left;

        &::v-deep {
            li {
                margin-bottom: 3px;
                line-height: 18px;
            }

            strong {
                font-weight: bold;
            }
        }
    }

    &__submit-button {
        display: block;
        width: fit-content;
        margin: 10px auto 0;
    }
}
